
.burger-menu__button {
    @media screen and (min-width: 1401px) {
        display: none;
    }
    display: block;
    position: fixed;
    top: 50px;
    right: 50px;
    z-index: 1000;
    width: 50px;
    height: 50px;
}

.burger-menu__button:hover, .burger-menu__img:hover {
    filter: brightness(0.7);
    -webkit-filter: brightness(0.7);
}

.burger-menu__active:hover, .burger-menu__img:hover{
    fill: rgb(250, 215, 215);
}

.burger-menu__nav {
    padding-top: 80px;
    position: fixed;
    z-index: 20;
    display: flex;
    flex-flow: column-reverse;
    height: 100%;
    background-color:aquamarine;
    top: 0;
    right: -100%;
}

.burger-menu__active .burger-menu__nav {
    right: 0;
}

